@-webkit-keyframes aitf {
	0% {
    -webkit-transform: translate(0) translateY(0);
            transform: translate(0) translateY(0)
  }
  20% {
    -webkit-transform: translate(-20px) translateY(20px);
            transform: translate(-20px) translateY(20px)
  }
	40% {
    -webkit-transform: translate(-10px) translateY(10px);
            transform: translate(-10px) translateY(10px)
  }
  60% {
    -webkit-transform: translate(8px) translateY(8px);
            transform: translate(8px) translateY(8px)
  }
  80% {
    -webkit-transform: translate(20px) translateY(20px);
            transform: translate(20px) translateY(20px)
  }
  100% {
    -webkit-transform: translate(0) translateY(0);
            transform: translate(0) translateY(0)
  }
}
@keyframes aitf {
	0% {
    -webkit-transform: translate(0) translateY(0);
            transform: translate(0) translateY(0)
  }
  20% {
    -webkit-transform: translate(-20px) translateY(20px);
            transform: translate(-20px) translateY(20px)
  }
	40% {
    -webkit-transform: translate(-10px) translateY(10px);
            transform: translate(-10px) translateY(10px)
  }
  60% {
    -webkit-transform: translate(8px) translateY(8px);
            transform: translate(8px) translateY(8px)
  }
  80% {
    -webkit-transform: translate(20px) translateY(20px);
            transform: translate(20px) translateY(20px)
  }
  100% {
    -webkit-transform: translate(0) translateY(0);
            transform: translate(0) translateY(0)
  }
}
